import React, { Component } from 'react';
import './Movie.styles.css';
class MovieList extends Component {

    render()
    {
       return(
        <div className="col-md-4 mt-3">
            <div className="card CardHeight" >
        <img className="img-fluid img-thumbnail ImgHieght card-img-top w-100"  src={this.props.image} alt={this.props.children}/>
        <div className="card-body">
       <h5 className="card-title">{this.props.children}</h5>
       <p className="card-text">{this.props.summary.substring(0,100)}</p>
          <a href={this.props.urldata} target="_blank" rel="noopener noreferrer" className="btn btn-primary">Go</a>
        </div>
      </div>
        </div>
        
       )
        
    }

        
           
        
    }

export default MovieList;