import React, { Component } from 'react';
import Movie from '../Movie/Movie.component';
import Pagination from 'react-js-pagination';
import './Home.styles.css';

import {connect} from 'react-redux';

const mapStatetoProps =(state)=>{
   return {
       movieData:state
   }
}
const mapDispatchToProps = (dispatch)=>{
return {
    changeMovieList:(moviedata)=>{dispatch({'type':'CHANGE_MOVIE','payload':moviedata})}
}
}
class Home extends Component {
  
   componentDidMount(){
       console.log("Load",this.props);
       fetch('https://yts.mx/api/v2/list_movies.json?limit=9')
       .then((res)=>res.json())
       .then((result)=>{
        this.props.changeMovieList({
            logged:true,
            MovieList:result,
            TotalList:result.data.movie_count,
            ActivePage:1,
            Query:null
           })
       },
       (error)=>{
        // console.log(error);
       })
   }
   
   

   handleNextPage(PageNumber){
    console.log("click",this.props);
    fetch(this.props.movieData.Query==null?'https://yts.mx/api/v2/list_movies.json?limit=9&page='+PageNumber:'https://yts.mx/api/v2/list_movies.json?limit=9&page='+PageNumber+'&query_term='+this.props.movieData.Query)
    .then((res)=>res.json())
    .then((result)=>{
     this.props.changeMovieList({
        logged:true,
        MovieList:result,
        TotalList:result.data.movie_count,
        ActivePage:PageNumber,
        Query:this.props.movieData.Query
    })
    
    },
    (error)=>{
     // console.log(error);
    })
   }
    render(){
        if(!this.props.movieData.logged){
            return (
                <h1>.....loading</h1>
                )
        }else{
            // console.log(this.state.MovieList);
        return (
            <div className="container">
                <div className="row">
                <Movie>{ this.props.movieData.MovieList}</Movie>
                </div> 
                <div className="row">
                <nav aria-label="Page navigation example">
                    <Pagination itemClass="page-item" linkClass="page-link" 
        activePage={this.props.movieData.ActivePage}
          itemsCountPerPage={9}
          totalItemsCount={this.props.movieData.TotalList}
          pageRangeDisplayed={5}
          onChange={this.handleNextPage.bind(this)}
        ></Pagination>
        </nav>
                </div>               
            </div>                 
             )   
        }        
    }   
}

export default connect(mapStatetoProps,mapDispatchToProps)(Home);
   
           
               
               
           
           
               
               
               
                   
                   
               
               
               
               
               
               
                   
                           
                           
                       
               
           
           
       
           
           
               
               
           
               
               
           
               
           
               
                   
               
           
       
           
           
               
               
                   
                   
                       
                               
                           
                           
                       
                   
               
           
               
                   
               
           
       
           
           
       
           
       
           
                   
                   
                       
                       
                       
                               
                               
                           
                   
                       
                   
                       
                               
                       
                   
                       
                       
                       
                       
                   
                       
                       
                   
                       
                           
                           
                       
                   
                       
                       
                           
                           
                       
                   
                       
                           
                           
                           
                           
                               
                           
                       
                   
                       
                       
                           
                           
                               
                           
                       
                   
               
           
               
                   
               
           
       
           
                       
                       
                           
                       
                   
                   
                   
                       
                       
                           
                           
                               
                               
                               
                               
                                   
                               
                           
                       
                   
                       
                       
                           
                           
                               
                               
                                       
                                       
                                   
                               
                           
                       
                   
                       
                       
                       
                   
                       
                           
                       
                   
                       
                           
                           
                               
                           
                       
                   
               
           
               
               
                   
               
           
       
           
                   
                   
                       
                           
                           
                               
                               
                                   
                                       
                                       
                                   
                                   
                               
                           
                       
                   
                       
                   
                       
                           
                       
                       
                   
                       
                       
                   
                       
                       
                   
                       
                           
                           
                               
                           
                       
                   
                       
                               
                               
                                   
                                   
                                       
                                   
                               
                           
                   
                       
                           
                           
                               
                               
                                   
                               
                           
                               
                                   
                               
                           
                       
                   
               
           
       
           
                   
                   
                       
                       
                           
                           
                       
                   
                       
                       
                           
                           
                               
                               
                           
                       
                   
                       
                       
                   
                       
                   
                       
                   
                       
                   
                       
                   
                       
                       
                           
                           
                           
                       
                           
                           
                       
                   
                       
                   
                       
                       
                           
                               
                           
                               
                           
                               
                           
                           
                       
                   
                       
                       
                       
                   
                       
                           
                           
                               
                           
                       
                   
               
           
               
               
               
               
           
       
           
                       
                   
                   
                   
                       
                               
                           
                               
                           
                   
                       
                       
                       
                       
                           
                           
                           
                           
                           
                           
                           
                               
                               
                           
                       
                   
                       
                   
                       
                   
                       
                           
                               
                           
                       
                   
                       
                   
                       
                   
                       
                   
                       
                   
                       
                       
                       
                   
                       
                   
                       
                   
                       
                           
                           
                               
                           
                       
                   
               
           
       
           
                   
                   
                   
                       
                       
                       
                   
                       
                   
                       
                           
                           
                               
                           
                       
                   
               
           
       
           
                       
                   
                   
                   
                       
                   
                       
                           
                       
                       
                   
                       
                       
                           
                       
                   
                       
                       
                       
                   
                       
                           
                           
                               
                           
                       
                   
               
           
               
                   
               
           
       
           
                   
                   
                       
                       
                           
                           
                               
                           
                           
                           
                               
                               
                                   
                               
                           
                       
                   
                       
                   
                       
                           
                           
                       
                       
                           
                       
                   
                       
                           
                           
                               
                           
                       
                   
               
           
       
    
