const istate = {
    logged: false,
    MovieList: '',
    ActivePage: '',
    TotalList: '',
    Query: ''
}
const reducer = (state = istate, action) => {
    if (action.type === "CHANGE_MOVIE") {
        return {
            logged: action.payload.logged,
            MovieList: action.payload.MovieList.data.movies,
            ActivePage: action.payload.MovieList.data.page_number,
            TotalList: action.payload.MovieList.data.movie_count,
            Query: action.payload.Query
        }
    }
    return state;
}

export default reducer;