import React, { Component} from 'react';
import {NavLink} from 'react-router-dom';
import Logo from '../logo.svg';
import {connect} from 'react-redux';

//map state to props
const mapStatetoProp= (state)=>{
  return {
    name:state
  } 
}
const mapDispatchToProps = (dispatch)=>{
  return {
      changeMovieList:(moviedata)=>{dispatch({'type':'CHANGE_MOVIE','payload':moviedata})}
  }
  }
class Header extends Component{

state = {
  inputValue:''
}
handleInputVale = (e)=> {
this.setState({
  inputValue:e.target.value
})
}

SendRequest= ()=>{
  if(this.state.inputValue!==''){
  fetch('https://yts.mx/api/v2/list_movies.json?limit=9&query_term='+this.state.inputValue)
  .then((res)=>res.json())
  .then((result)=>{
   this.props.changeMovieList({
       logged:true,
       MovieList:result,
       TotalList:result.data.movie_count,
       ActivePage:1,
       Query:this.state.inputValue
      })
  },
  (error)=>{
   // console.log(error);
  })
}
}
 

  render(){
    return (
      <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <NavLink className="navbar-brand" to="/">
      <img src={Logo} width="70" height="30" alt=""/>
      </NavLink>
      <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav mr-auto">
          <li className="nav-item active">
            <NavLink className="nav-link" to="/">Home <span className="sr-only">(current)</span></NavLink>
          </li>
          <li className="nav-item">
            <NavLink className="nav-link" to="/about">Link</NavLink>
          </li>
        </ul>
        <form className="form-inline my-2 my-lg-0" method="post">
          <input className="form-control mr-sm-2" type="search" placeholder="Search" onChange={this.handleInputVale}  aria-label="Search"/>
          <button className="btn btn-outline-primary my-2 my-sm-0" type="button" onClick={this.SendRequest} value={this.state.inputValue}  >Search</button>
        </form>
      </div>
    </nav>
  )
  }
}
export default connect(mapStatetoProp,mapDispatchToProps)(Header);
// export default Header;